import React from 'react';
import Articles from '../components/Articles';
import Navbar from '../components/Navbar';

const Espagnol = () => {
    const infos = {
        Espagnol: {
            cour: {
                p1: {
                    titre: "mon titre d'espagnol",
                    paragraphe: "mon paragraphe d'espagnol"
                }
            }
        }
    }  
    return (
        <div>
            <h1>Espagnol...</h1>
                <Navbar />
            <i className="fas fa-arrow-down"></i>
                <Articles infos={infos.Espagnol.cour.p1} />
        </div>
    );
};

export default Espagnol;